@import "../../style/index";

.navLink {
  color: $text-main;
  font-weight: bold;
}

.navFooter {
  background: #000;
  padding-top: 80px;
  padding-bottom: 30px;
  h3 {
    font-size: 16px;
    color: #fff;
    font-weight: bold;
  }
  ul {
    list-style: none;
    padding-left: 0;
    opacity: 0.5;
    margin-top: 20px;
  }
  ul li {
    font-size: 16px;
    color: #fff;
    line-height: 2.5;
  }

  a {
    text-decoration: none;
    color: #fff;
  }

  .record {
    display: none !important;
    //display: flex;
    //justify-content: center;
    //align-items: center;
    margin-top: 100px;
    p {
      color: #4C537D;
      margin-bottom: 0px;
      font-size: 14px;
    }
    img {
      width: 15px;
      margin-left: 0px;
      margin-right: 8px;
      position: relative;
      top: -2px;

    }
  }

  .recordPC {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 100px;
    p {
      color: #4C537D;
      margin-bottom: 0px;
      font-size: 14px;
    }
    img {
      width: 20px;
      margin-left: 28px;
      margin-right: 13px;
    }
  }

  .social {
    margin-top: 25px;
    margin-bottom: 20px;
    a {
      display: inline-block;
      width: 30px;
      height: 30px;
      text-align: center;
      margin-right: 10px;
    }
  }

  .socialSX {
    display: none;
  }
}

@media (max-width: 575.98px) {
  .navFooter {
    padding-top: 0px;

    .socialSX {
      padding-top: 20px;
      display: block;
      text-align: center;
      a{
        margin-left: 10px;
        margin-right: 10px;
      }
    }
  }
}

@media (max-width: 768px) {
  .navFooter {
    .record {
      margin-top: 20px;
      display: flex !important;
    }

    .recordPC {
      display: none !important;
    }
  }

}




